import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { Carousel } from "react-bootstrap";

import { firebaseapp } from "../../utils";
import "firebase/storage";
import "firebase/database";

const CollaborationsContainer = styled.div`
	display: flex;
	flex-direction: ${props => (props.theme.screens.md ? "column" : "row")};
	flex-wrap: wrap;
	padding: ${props => (props.theme.screens.md ? "3vh" : "7vh")};
`;

const TextContainer = styled.div`
	display: flex;
	flex-direction: column;
	align-content: ${props => (props.theme.screens.sm ? "center" : "left")};
	width: ${props => props.theme.screens.lg ? "100%": "40vw"}
`;

const TextBig = styled.h1`
	font-family: "Open Sans", sans-serif;
	font-weight: 700;
	font-size: ${props => (props.theme.screens.md ? "2rem" : "2.3rem")};
	text-align: ${props => (props.theme.screens.md ? "center" : "left")};
	display: inline-block;
	border: ${props => (props.theme.screens.md ? "3px solid black" : "none")};
	border-bottom: 3px solid black;
	padding: ${props => (props.theme.screens.md ? "1.2rem" : "0rem 0rem 1.2rem 0rem")};
`;

const TextSmall = styled.h3`
	font-family: "Crimson Text", serif;
	font-size: 1.4rem;
	font-weight: 400;
	text-align: ${props => (props.center ? "center" : "left")};
	margin: 2rem;
	font-style: ${props => (props.italic ? "italic" : "normal")};
`;

const SwiperContainer = styled.div`
	flex: 1;
	margin: ${props => (props.theme.screens.md ? "2rem 0rem 0rem 0rem" : "0rem 3rem 0rem 3rem")};
`;

const Icons = styled.div`
	color: black;
	font-size: 2rem;

	a {
		color: black;
	}

	a:hover {
		color: ${props => props.theme.colors.black};
	}

	i {
		padding: 1.5rem 1.5rem 0rem 0rem;
	}
`;

const OurCollaborations: React.FC = () => {
	const [imgs, setImgs] = useState([]);
	const [collaborationCompanies, setCollaborationCompanies] = useState([]);

	useEffect(() => {
		firebaseapp
			.storage()
			.ref("landing_page/collaborations")
			.listAll()
			.then(({ items }) => {
				let imgRefs = items.map(i => i.getDownloadURL());
				Promise.all(imgRefs).then(ar => {
					setImgs(ar);
				});
			});

		firebaseapp
			.database()
			.ref("collaborations")
			.once("value")
			.then(snapshot => setCollaborationCompanies(snapshot.val()));
	}, []);

	return (
		<CollaborationsContainer>
			<TextContainer>
				<TextBig>Our Collaborations</TextBig>
				<TextSmall italic center>
					"Alone we can do little, <br></br> but together we can do so much" <br></br>
					<br></br> <span>&#8212;</span> Hellen Keller
				</TextSmall>

				<TextSmall>
					Lorem ipsum dolor sit amet consectetur,<br></br> ducimus explicabo saepe harum dolores minus quaerat
					consequatur.
				</TextSmall>

				<TextSmall>
					Follow us on our instagram and facebook pages to have a look at our other collaborations.
					
					<Icons>
						<a href="https://www.instagram.com/the.yellow.filter" target="_blank" rel="noopener noreferrer">
							<i className="fab fa-instagram"></i>
						</a>

						<a href="#">
							<i className="fab fa-facebook"></i>
						</a>
					</Icons>
				</TextSmall>
			</TextContainer>

			<SwiperContainer>
				<Carousel>
					{imgs.map((url, index) => (
						<Carousel.Item interval={1500}>
							<img className="d-block w-100" src={url} alt="First slide" />
							<Carousel.Caption>
								<p>{collaborationCompanies[index]}</p>
							</Carousel.Caption>
						</Carousel.Item>
					))}
				</Carousel>
			</SwiperContainer>
		</CollaborationsContainer>
	);
};

export default OurCollaborations;
